import React, { Component } from 'react';
import './App.css';

import logo from './images/justin-and-dragons-logo.png';

import Party    from './Party';
import Timeline from './Timeline';

class App extends Component {

  timelineEvents = [];
  lastScrollX    = 0;
  ticking        = false;
  activeElement;

  handleScroll = () => {
    this.lastScrollX = window.scrollX;
    if (!this.ticking) {
      window.requestAnimationFrame(() => {
        this.ticking = false;
      });
      this.ticking = true;
      this.checkIfActive();
    }
  };

  getTimelineEvents = () => {
    this.timelineEvents = document.getElementsByClassName("timelineEvent");
  }

  // Check the scroll position of all timeline elements
  // Mark element as active if it is within 500px of the edge of the screen
  checkIfActive = () => {
    let i = 0;
    for (let element of this.timelineEvents){
      let eventPosition = element.getBoundingClientRect().left;
      if(element){
        if(eventPosition >= 0 && eventPosition <= 500){ // Max range is equal to the width of every event element
          element.classList.add("active");

          let partyMembers = this.refs.timeline.getActivePartyMembers(i);
          this.refs.party.updateActiveMembers(partyMembers);
        }
        else{
          element.classList.remove("active");
        }
      }
      i++;
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    this.getTimelineEvents();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} alt="Justin and Dragons Logo" />
        </header>
        <main>
          <Timeline ref="timeline" />
          <Party ref="party" />
        </main>
      </div>
    );
  }
}

export default App;
