import React, { Component } from 'react';
import './Party.css';

import broxigar from './images/sprites/Broxigar.png';
import hada     from './images/sprites/Hada.png';

class Party extends Component {

  partyMembers = [
    // 1st Campaign
    "Broxigar",
    "Rhogog",
    "Raven",
    "Lothkorius",
    "Hada",
    "Carmine",
    "Sawyer",
    "Thorkel",
    "Rhazer",
    "Quiet",
    "Bolt",

    // 2nd Campaign and onwards
    "Komrk",
    "Thagus",
    "Rozalin",
    "Magnus",
    "Toadstool",
    "Boskov",
    "Kelibourne",
    "Elect",
    "Ogda",
    "Tenor",
    "Tenenbaum",
    "Hamilcar",
    "Zo",
    "Denasi",
    "Bthuhl",
    "Cornell",
    "Cero",
    "Kormag",
    "Aoi",
    "Karasu",
    "Sirus",
    "Kasel",
    "Celestia"
  ];

  activeMembers = [];

  leaveParty(){}

  joinParty(){}

  killCharacter(){}

  reviveCharacter(){}

  updateActiveMembers = (members) => {
    let availableElements = document.getElementById("partyList").childNodes;
    console.log("active members", members);
    for(let element of availableElements){
      element.style.display = "none";
      if(members.indexOf(element.id) !== -1){
        element.style.display = "inline";
      }
    }
  }

  render() {
    return (
      <div className="Party">
        <ul id="partyList">
          <li id="Hada"><img src={hada} alt="Hada" /></li>
          <li id="Broxigar"><img src={broxigar} alt="Broxigar" /></li>
          <li id="Rhogog">Rhogog</li>
          <li id="Raven">Raven</li>
          <li id="Lothkorius">Lothkorius</li>
          <li id="Carmine">Carmine</li>
          <li id="Sawyer">Sawyer</li>
          <li id="Thorkel">Thorkel</li>
          <li id="Rhazer">Rhazer</li>
          <li id="Quiet">Quiet</li>
          <li id="Bolt">Bolt</li>
        </ul>
      </div>
    );
  }
}

export default Party;
